@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');

.mt-shopping-list {
	width: 100%;
	background-image: linear-gradient(45deg, #131212, rgba(17, 17, 17, 0.2), #353536);
	font-family: 'Audiowide';
}

.mt-list {
	list-style-type: none;
	padding: 32px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.mt-infos{
	color: #aaa9af;
	text-shadow: 1px 1px 10px #f0eff5 ;
	font-size: 1.5em;
	text-align: center;
	font-family: 'Chakra Petch';
	font-weight:600;
	margin: 50px 0 0 420px;
	border: solid 2px rgb(25, 25, 27);
	width: 17%;
	border-radius: 5px;
	background-color: rgb(25, 25, 27);
	padding: 5px;
	box-shadow: 1px 1px #3e3e3e, 2px 2px #3e3e3e, 3px 3px #3e3e3e, 4px 4px #3e3e3e, 5px 5px #3e3e3e, 6px 6px #3e3e3e, 7px 7px #3e3e3e, 8px 8px #3e3e3e;
}
@media screen and (max-width: 1300px) {
	.mt-infos{
		margin: 35px 0 0 400px;
	}
}
@media screen and (max-width: 900px) {
	.mt-infos{
		margin: 35px 0 0 200px;
		width: 30%;
	}
}
@media screen and (max-width: 770px) {
	.mt-infos{
		margin: 35px 0 0 175px;
		width: 30%;
		font-size: 1em;
		box-shadow: 1px 1px #7e7d7d, 2px 2px #7e7d7d, 3px 3px #7e7d7d, 4px 4px #7e7d7d, 5px 5px #7e7d7d, 6px 6px #7e7d7d, 7px 7px #7e7d7d, 8px 8px #7e7d7d;
	}
}
@media screen and (max-width: 520px) {
	.mt-infos{
		margin: 35px 0 0 125px;
		width: 30%;
		font-size: 0.8em;
		box-shadow: 1px 1px #7e7d7d, 2px 2px #7e7d7d, 3px 3px #7e7d7d, 4px 4px #7e7d7d, 5px 5px #7e7d7d, 6px 6px #7e7d7d, 7px 7px #7e7d7d, 8px 8px #7e7d7d;
	}
}
