body{
	overflow-x: hidden;
}
.mt-banner {
	overflow-x: hidden;
	width: 100%;
	color: black;
	text-align: right;
	padding: 32px;
	border-bottom: solid 3px #ffffff;
	display: flex;
	justify-content: flex-end;
	flex-direction: row;
	align-items: center;
	background-image: linear-gradient(45deg, #131212, #36363a);
}

.mt-title {
	text-shadow: 1px 1px 10px #f0eff5 ;
	padding-left: 62px;
	background-image: linear-gradient(45deg, #908e8e, rgba(177, 172, 172, 0.877), #f0f0f6);
	background-clip: text;
	color: transparent;
	font-size: 3em;
	margin: 0 150px 0 0;
}
.slogan{
	color: #aaa9af;
	text-shadow: 1px 1px 10px #f0eff5 ;
	margin: 0 15px 0 0;
	font-size: 1em;
}

.mt-logo {
	height: 95px;
	width: 95px;
}
.right-star{
	padding: 0 100px 0 15px;
}
.left-star{
	margin: 0 25px 0 0;
} 
@media screen and (max-width: 1300px) {
	.mt-title {
		font-size: 2em;
	}
	.slogan{
		margin: 0 225px 0 -75px;
	}
	.mt-logo {
		width: 75px;
		height: 75px;
		margin: 0 0 0 85px;
	}
	.right-star{
		display: none;
	}
	.left-star{
		display: none;
	} 
}
@media screen and (max-width: 900px) {
	.mt-title {
		font-size: 1.7em;
		margin: 0 200px 0 0;
	}
	.slogan{
		margin: 0 65px 0 -150px;
	}
	.mt-logo {
		width: 75px;
		height: 75px;
		margin: 0 0 0 0;
	}
	
}
@media screen and (max-width: 770px) {
	.mt-title {
		font-size: 1.6em;
		margin: 0 200px 0 0;
	}
	.slogan{
		margin: 0 65px 0 -150px;
		font-size: 0.7em;
	}
	.mt-logo {
		width: 65px;
		height: 65px;
		margin: 0 0 0 0;
	}
}
@media screen and (max-width: 520px) {
	.mt-title {
		font-size: 1.4em;
		margin: 0 200px 0 0;
	}
	.slogan{
		margin: 0 65px 0 -150px;
		font-size: 0.5em;
		text-align: center;
	}
	.mt-logo {
		width: 45px;
		height: 45px;
		margin: 0 0 0 0;
	}
}
@media screen and (max-width: 400px) {
	.slogan{
		margin: 0 15px 0 -175px;
		font-size: 0.5em;
		text-align: center;
	}
}
